
.thumb_wrp {
	position: relative;
	width: 100px;
	height: 100px;
}
.photo_input_wrp {
	position: relative;
	width: 100%;
	height: 100%;
	opacity: 0.5;
	border: 1px solid gray;
}
.photo_input_wrp:active {
	opacity: 1;
}
.photo_input_hline {
	width: 50%;
	height: 2px;
	top: 50%;
	left: 25%;
}
.photo_input_vline {
	width: 2px;
	height: 50%;
	top: 25%;
	left: 50%;
}
.photo_input_hline,
.photo_input_vline {
	position: absolute;
	background-color: #C7C7C6;
}
.photo_input {
	position: absolute;
	width: 100%;
	height: 100%;
	opacity: 0;
}
.thumb_img {
	position: absolute;
	border: 1px solid black;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: white;
}
.del_img_btn {
	position: absolute;
	width: 25px;
	height: 25px;
	line-height: 25px;
	top: 0;
	left: 100%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	z-index: 1;
	color: blue;
	text-align: center;
	font-size: 18px;
	padding: 0;
	margin: 0;
	font-weight: bold;
	border: 1px solid gray;
	border-radius: 50%;
	background: blue;
	color: white;
	opacity: 0.8;
}
.my-navbar {
	list-style: none;
	padding: 0;
	margin: 0;
	display: table;
	width: 100%;
}
.my-navbar li {
	padding: 1px;
	display: table-cell;
}
.my-navbar li a {
	width: 100%;
}



