
.chess {
	cursor: pointer;
	background-color: #FEF7CC;
	box-sizing: border-box;
	position: absolute;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
}
.chess-select {
	border: 2px solid red;
}
.chess-noselect {
	border: 2px solid #FDC44C;
}
.chess-label {
	white-space: nowrap;
	font-weight: bold;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}



