
.menu-cells-wrap {
	padding: 2px;
}
.menu-cell {
	padding: 5px;
	position: relative;
	border: 0px solid gray;
}
.menu-cell>div {
	box-sizing: content-box;
	position: absolute;
	width: 95%;
	height: 95%;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.menu-cell>div:active {
	background: gainsboro;
}
.menu-cell:before {
	content: "";
	display: inline-block;
	padding-bottom: 100%;
	width: .1px;
	vertical-align: middle;
}
.menu-icon {
	font-size: 50px;
	padding-bottom: 5px;
	color: gray;
}
.menu-desc {
	color: gray;
}
.menu-tip {
	margin: 0;
	padding: 0;
	color: orange;
}



