
.chat-list-wrap {
	padding: 5px 0;
	background: white;
}
.chat-item {
	padding: 5px;
}
.chat-name {
	display: inline-block;
	padding: 5px;
}
.chat-msg-content {
	display: inline-block;
	border-radius: 5px;
	text-align: left;
	padding: 5px;
	min-width: 30%;
	max-width: 75%;
	word-wrap: break-word;
}
.chat-time-wrap {
	background: gray;
	color: white;
}



