
.btn-home {
	position: absolute;
	top: 50%;
	left: 35%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.btn-menu {
	position: absolute;
	top: 50%;
	left: 20%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.current-tip {
	position: absolute;
	top: 50%;
	left: 70%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.current-tip>div {
	display: inline-block;
	vertical-align: middle;
}



