
svg .station-text {
	stroke: gray;
	stroke-width: 0.1;
	font-size: 12px;
	font-weight: normal;
	text-anchor: middle;
	/* 文本水平居中 */
	dominant-baseline: central;
	/* 文本垂直居中 */
}
svg .station {
	stroke: gray;
	stroke-width: 1;
	fill: white;
}
svg .station-dabenying {
	stroke: gray;
	stroke-width: 1;
	fill: white;
}
svg .station-xingying {
	stroke: gray;
	stroke-width: 1;
	fill: white;
}



