
.messagecenter-wrap {
	position: absolute;
	width: 80%;
	text-align: center;
	left: 50%;
	top: 10%;
	-webkit-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
}
.messagecenter-tip {
	width: 100%;
	text-align: center;
}
.messagecenter-tip-item {
	padding: 5px;
}
.messagecenter-error {
	width: 100%;
	text-align: center;
}
.messagecenter-error-item {
	padding: 5px;
}



